import ToolBar from '@/components/main/ToolBar'
import FooterComponent from '@/components/main/FooterComponent'
import AppBar from '@/components/main/AppBar'
import axios from 'axios'

export default {
data () {
    return {
        loading: true,
        tab: 0,
        cards: [],
        toleg_card: [
          { 
            title1: 'Dkany 3 alyk krende tlegi',
            title2: '100 TMT',
            title3: '04.11.2023 | 17:00 enli'
          },
          { 
            title1: 'Dkany 3 alyk krende tlegi',
            title2: '100 TMT',
            title3: '04.11.2023 | 17:00 enli'
          },
          { 
            title1: 'Dkany 3 alyk krende tlegi',
            title2: '100 TMT',
            title3: '04.11.2023 | 17:00 enli'
          },
        ],
        toleg_taryh_card: [
          { 
            info: 'Dkany 3 alyk krende tlegi',
            price: '100 TMT',
            date: '04.11.2023 | 17:00 enli',
            store: 'Nike Store',
            location: 'Agabat',
            status: 0,
            status_text: 'Tleg gemedi'
          },
          { 
            info: 'Dkany 3 alyk krende tlegi',
            price: '100 TMT',
            date: '04.11.2023 | 17:00 enli',
            store: 'Nike Store',
            location: 'Agabat',
            status: 1,
            status_text: 'Tleg gedi'
          },
          { 
            info: 'Dkany 3 alyk krende tlegi',
            price: '100 TMT',
            date: '04.11.2023 | 17:00 enli',
            store: 'Nike Store',
            location: 'Agabat',
            status: 0,
            status_text: 'Tleg gemedi'
          },
        ],
        input_comments: [
          {
            code: 'KEZ56EG',
            datetime: '16.11.2023 | 16:33',
            comment: 'Bolan eri ne dost, ousa gni barjak'
          },
          {
            code: 'KEZ56EG',
            datetime: '16.11.2023 | 16:33',
            comment: 'Bolan eri ne dost, ousa gni barjak'
          },
          {
            code: 'KEZ56EG',
            datetime: '16.11.2023 | 16:33',
            comment: 'Bolan eri ne dost, ousa gni barjak'
          },
        ],
        email: localStorage.getItem('email')
      }
    },
  watch: {
    '$route.query.tab': {
      handler: function() {
        this.getTab()
      }
    }
  },
  components: {
    ToolBar,
    FooterComponent,
    AppBar
  },
  created() {
    this.getTab(),
    this.getFavorites()
  },
  methods: {
    async getFavorites() {
      await axios.get('/publications/publications/favorites/')
        .then(response => {
          this.cards = response.data.results
        })
        .catch(error => {
          console.log(error)
        })
    },
    async getTab() {
      if (this.$route.query.tab == '0'){
        this.tab = 0
      }
      if (this.$route.query.tab == '1'){
        this.tab = 1
      }
      if (this.$route.query.tab == '2'){
        this.tab = 2
      }
    },
    logout() {
      localStorage.removeItem('token')
      localStorage.removeItem('email')
      this.$router.push('/')
    },
  }
}